import React from 'react';
import ReactDOM from 'react-dom';
import MakeList from './CreateList/makeLists.js'
import FileForm from './CreateForm/createFile.js'

export default class Main extends React.Component{

    constructor() {
      super();
      this.state = {Youken: null,
      	            score:null,
      	            bool:false,
                    result:null,
      };
    }

    GetselectedList(e) {
    this.setState({ Youken: e })
    }

    Getchildscore(e) {
    this.setState({ score: e ,
    	              bool:false,
    })
    }
     



    // Appにstateの情報をすべて渡す
    async sendState(){
    if(this.state.bool){
      alert('成績を入力してください');
    }else{

      let result = await this.jugment();

      this.setState({result:result});
      this.props.getResult(this.state);
      
    }
    
    }

  // メイン判定メソッド
   jugment() {

    return new Promise((resolve)=>{

    let category = this.createCategory(this.state.Youken).category;
    let Youken = this.createCategory(this.state.Youken).M_category;
    let score = this.state.score;

    let result = [];
    let count = 0;


    // 卒業要件をループさせる(必修科目)
    Youken_loop: for (let i = 0; i < Youken.length - 2; i++) {

      // 使用中の卒業要件
      let presentYouken = Youken[i];

      // 必修科目でなければ、次のグループへ
      if (this.isHissyuukamoku(presentYouken.group_min)) continue Youken_loop;

      //  成績データをループさせる
      score_loop: for (let j = 0; j < score.length; j++) {

        // 使用中の成績データ
        let presentScore = score[j];

        // グループの合計単位数が卒業要件の取得単位数の下限を満たしたか判定
        let isGroupFilled = (presentYouken.group_min <= presentYouken.group_sum);
        // 満たしていた場合成績ループを終了
        if (isGroupFilled) break;

        // 要件の除外項目に該当するかの判定
        if (this.isRemove(presentYouken.remove, presentScore)) continue score_loop;

        // // ＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝未実装＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝
        // // // 要件の制限要素に該当するかの判定
        // // if(isRestriction(Youken[i].restriction,score[j])){
        // //   continue score_loop;
        // // }
        // // ＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝未実装＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝

        // 成績が卒業要件に当てはまるかの検証
        let isMaching = this.Match(presentScore, presentYouken.number, presentYouken.name);

        if (isMaching) {
          // 一致した場合その成績をresultに格納
          this.MakeResult(presentYouken, presentScore, category, result, count);
          count++;
          //判定した成績を削除
          score.splice(j, 1);
          j--;
        }

      }

    }

    // 卒業要件をループさせる（その他科目）
    Youken_loop: for (let i = 0; i < Youken.length - 2; i++) {

      // 使用中の卒業要件
      let presentYouken = Youken[i];

      //  成績データをループさせる
      score_loop: for (let j = 0; j < score.length; j++) {

        // 使用中の成績データ
        let presentScore = score[j];
        // 使用中のカテゴリー
        let presentCategory = category[presentYouken.No];

        // グループの上限判定
        if (presentYouken.group_sum >= presentYouken.group_max) break;

        // カテゴリーの上限判定
        if (presentCategory.category1_sum >= presentCategory.category1_max) break;

        // 要件の除外項目に該当するかの判定
        if (this.isRemove(presentYouken.remove, presentScore)) continue score_loop;

        // // ＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝未実装＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝
        // // // 要件の制限要素に該当するかの判定
        // // if(isRestriction(Youken[i].restriction,score[j])){
        // //   continue score_loop;
        // // }
        // // ＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝未実装＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝

        // 成績が卒業要件に当てはまるかの検証
        let isMaching = this.Match(presentScore, presentYouken.number, presentYouken.name);

        if (isMaching) {
          // 一致した場合その成績をresultに格納
          this.MakeResult(presentYouken, presentScore, category, result, count);
          count++;
          //判定した成績を削除
          score.splice(j, 1);
          j--;

        }
      }
    }

    console.log(result);
    
    // this.setState({ result: result });
    resolve(result);

  })

  }

  // カテゴリー作成メソッド
  createCategory(e) {

    // 消した個数を管理する変数
    let del_number = 0;

    let category = e.concat();
    let M_category = e.concat()

    for (let i = 0; i < category.length - 2; i++) {

      for (let j = i + 1; j < category.length - 2; j++) {


        M_category[i + del_number].No = i;

        //最適化ポイント
        if (j > i + 2) break;


        // カテゴリーが一致するかの判定
        const check1 = category[i].category1 === category[j].category1;
        const check2 = category[i].category2 === category[j].category2;
        const check3 = category[i].category3 === category[j].category3;

        // カテゴリーが一致した場合
        if (check1 && check2 && check3) {
          M_category[j + del_number].No = i;

          // 判定したグループを削除する
          category.splice(j, 1);
          del_number++

          // 消したグループの分カウントを一つ戻す
          j--;
        };
      };
    };

    return { M_category, category }
  }

  // 必修科目判定メソッド
  isHissyuukamoku(Youken) {
    if (Youken == 0) return true;
  }

  // 除外判定メソッド
  isRemove(remove, score) {
    // 除外要素が要件にない場合即リターン
    if (remove == "-") return false;

    // 除外要素があった場合はtrueを返す
    for (let k = 0; k < remove.length; k++) {

      // 除外要素が成績にあるかの判定
      let remove_exp = new RegExp('(^)' + remove[k]);
      let isRemovement = remove_exp.test(score.科目番号) || remove_exp.test(score.科目名);

      if (isRemovement) return true;
    }
    return false;
  }

  //一致判定メソッド 　
  Match(score, number, name) {

    // 科目番号のマッチング 
    for (let k = 0; k < number.length; k++) {

      let number_exp = new RegExp('(^)' + number[k]);
      let isMatch = number_exp.test(score.科目番号);
      if (isMatch) return true;

    }

    // 科目名のマッチング
    for (let k = 0; k < name.length; k++) {

      let name_exp = new RegExp('(^)' + name[k]);
      let isMatch = name_exp.test(score.科目名);
      if (isMatch) return true;
    }

  }

  // 一致処理メソッド
  MakeResult(Youken, score, category, result, count) {

    // 一致した成績の単位数をグループ合計に格納
    Youken.group_sum += parseFloat(score.単位数);

    // 一致した成績の単位数をカテゴリー合計に格納
    category[Youken.No].category1_sum += parseFloat(score.単位数);

    // 一致した成績の単位数を必修・選択・自由カテゴリー合計に格納
    category[category.length-2][Youken.category3] += parseFloat(score.単位数);

    // 一致した成績の単位数を全体合計カテゴリー合計に格納
    category[category.length-2].合計 += parseFloat(score.単位数);

    // 一致した成績をresultに格納
    result[count] = {
      'category1': Youken.category1, 'category2': Youken.category2, 'category3': Youken.category3,
      'about': Youken.group, '科目番号': score.科目番号, '科目名': score.科目名, '単位数': score.単位数
    };

  }


    render(){

      return (

   <React.Fragment>

          <nav className="navbar navbar-dark navbar-expand-lg fixed-top portfolio-navbar gradient">
          <div className="container"><a className="navbar-brand logo" href="#">UNIVERSITY OF TSUKUBA</a>
              <div className="collapse navbar-collapse" id="navbarNav">
                  <ul className="nav navbar-nav ml-auto"></ul>
              </div>
          </div>
      </nav>


      <main className="page contact-page">
          <section className="portfolio-block contact">
              <div className="container">
                  <div className="heading">
                      <h2 style={{marginBottom: 0+'px'}}>CREDIT CHECKER</h2>
                  </div>

                     <form style={{padding: 50+'px'}}>

                     <MakeList getselected={this.GetselectedList.bind(this)} />

                     <FileForm hoge={this.Getchildscore.bind(this)} />

                     <div className="form-group"><button className="btn btn-primary btn-block btn-lg" type="button" 
                     style={{marginTop: 45+'px'}}onClick={this.sendState.bind(this)}>Submit Form</button></div>

                  </form>

              </div>
          </section>
      </main>
      <footer className="page-footer">
          <div className="container">
              <div className="links"></div>
              <p style={{marginBottom: 4+'px'}}>筑波大学国際総合学類卒</p>
              <p style={{marginBottom: 4+'px'}}>バグ等お問い合わせはこちらへ</p>
              <div className="social-icons" style={{marginTop: 0+'px'}}><a href="#"><i className="icon ion-social-twitter"></i></a></div>
          </div>
      </footer>
        </React.Fragment>
  )

     }
    }